@import "icomoon-variables";

@font-face {
  font-family: '@{icomoon-font-family}';
  src:  url('@{icomoon-font-path}/@{icomoon-font-family}.eot?hc5t27');
  src:  url('@{icomoon-font-path}/@{icomoon-font-family}.eot?hc5t27#iefix') format('embedded-opentype'),
    url('@{icomoon-font-path}/@{icomoon-font-family}.ttf?hc5t27') format('truetype'),
    url('@{icomoon-font-path}/@{icomoon-font-family}.woff?hc5t27') format('woff'),
    url('@{icomoon-font-path}/@{icomoon-font-family}.svg?hc5t27#@{icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '@{icomoon-font-family}' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-mail {
  &:before {
    content: @icon-mail; 
  }
}
.icon-x {
  &:before {
    content: @icon-x; 
  }
}
.icon-facebook2 {
  &:before {
    content: @icon-facebook2; 
  }
}
.icon-instagram {
  &:before {
    content: @icon-instagram; 
  }
}

